@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Josefin+Sans:400,400i,600,600i");
html, body {
  margin: 0;
  font-family: "Josefin Sans", sans-serif;
}

.header {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  align-content: flex-start;
  height: 100px;
  min-height: 100px;
  color: #eee;
}

.header:after {
  content: "";
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 70%, #00994d 100%);
}

.header:before {
  content: "";
  width: 100%;
  height: 320%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0);
  backface-visibility: hidden;
  scale: 1, 1;
  transform: translateZ(0);
  background: #1B2030 url(/src/Pages/Images/pexels-justin-wolfert-9553449.jpg) 50% 0 no-repeat;
  background-size: 100%;
  background-attachment: fixed;
  -webkit-animation: grow 80s linear 10ms infinite;
          animation: grow 80s linear 10ms infinite;
  transition: all 0.4s ease-in-out;
  z-index: -2;
}

.header a {
  text-decoration: none;
  cursor: pointer;
  color: #eee;
  text-shadow: 0px 0px 10px black;
  font-size: 1.2rem;
}
.header a:hover {
  transform: scale(1.1);
  transition: 0.2s ease-out;
}

.info {
  height: 100%;
  width: 100%;
  text-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
}
.info .navigationWrapper {
  display: flex;
  flex-direction: row;
  -moz-column-gap: 2rem;
       column-gap: 2rem;
}
.info h1 {
  font-size: 3rem;
}

@-webkit-keyframes grow {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-200px);
  }
}

@keyframes grow {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-200px);
  }
}
.content {
  padding: 5% 10%;
  text-align: justify;
}

.btn {
  color: #333;
  border: 2px solid;
  border-radius: 3px;
  text-decoration: none;
  display: inline-block;
  padding: 5px 10px;
  font-weight: 600;
}

.twtr {
  margin-top: 100px;
}

.btn.twtr:after {
  content: "üê¶";
  padding-left: 5px;
}

@media screen and (max-width: 500px) {
  .header::before {
    background-size: 150%;
  }
}